









































.bg {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1000;
  background-size: 100% 100%;
  background-color: #aaa;
  & .change {
    position: fixed;
    top: 1em;
    right: 0;
    color: #336633;
    transform: rotate(45deg);
  }
}
